"use client"

import React from "react"
import { EditorBlock } from "@/lib/scorm/types"
import { useLocale } from "@/hooks/use-locale"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"

// sub-panels
import TextPropertiesPanel from "./subpanels/text-properties-panel"

interface PropertiesPanelProps {
  selectedBlock: EditorBlock | null
  onBlockChange: (block: EditorBlock) => void
}

export function PropertiesPanel({
  selectedBlock,
  onBlockChange,
}: PropertiesPanelProps) {
  const { t } = useLocale()

  if (!selectedBlock) {
    return (
      <div className="h-full flex flex-col items-center justify-center px-4 py-6 text-center">
        <div className="mb-3">
          <Badge variant="outline" className="text-[11px] px-2 py-0 rounded-full">
            {t("scorm.props.title") || "Properties"}
          </Badge>
        </div>
        <p className="text-sm font-semibold text-foreground mb-1">
          {t("scorm.props.emptyTitle") || "No block selected"}
        </p>
        <p className="text-[11px] text-muted-foreground max-w-xs">
          {t("scorm.props.desc") ||
            "Click any block in the canvas to edit it here."}
        </p>
      </div>
    )
  }

  const typeLabelMap: Record<EditorBlock["type"], string> = {
    text: t("scorm.props.block.text.label") || "Text",
    image: t("scorm.props.block.image.label") || "Image",
    video: t("scorm.props.block.video.label") || "Video",
    quiz: t("scorm.props.block.quiz.label") || "Quiz",
  }

  const renderSubPanel = () => {
    switch (selectedBlock.type) {
      case "text":
        return (
          <TextPropertiesPanel
            block={selectedBlock}
            onChange={onBlockChange}
          />
        )

      // the rest will be added later:
      // case "image": return <ImagePropertiesPanel ... />
      // case "video": return <VideoPropertiesPanel ... />
      // case "quiz": return <QuizPropertiesPanel ... />

      default:
        return (
          <p className="text-[11px] text-muted-foreground">
            {t("scorm.props.unsupported") ||
              "This block type has no editable options yet."}
          </p>
        )
    }
  }

  return (
    <div className="h-full flex flex-col bg-card/80">
      {/* Header */}
      <div className="px-4 pt-4 pb-3">
        <div className="flex items-center justify-between gap-2">
          <div>
            <h3 className="text-sm font-semibold">
              {t("scorm.props.title") || "Block Settings"}
            </h3>
            <p className="text-[11px] text-muted-foreground">
              {t("scorm.props.subtitle") ||
                "Customize the selected learning block."}
            </p>
          </div>

          <Badge variant="secondary" className="text-[10px] px-2 py-0 rounded-full">
            {typeLabelMap[selectedBlock.type]}
          </Badge>
        </div>
      </div>

      <Separator className="mb-3" />

      {/* Body */}
      <div className="flex-1 overflow-y-auto px-4 pb-4 text-xs">
        {renderSubPanel()}
      </div>
    </div>
  )
}
