"use client"

import React from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { updateHtmlStyle } from "@/lib/scorm/text-utils"
import type { TextBlock } from "@/lib/scorm/types"
import { AlignLeft, AlignCenter, AlignRight, AlignJustify, Bold, Italic, Underline } from "lucide-react"

interface TextPanelProps {
  block: TextBlock
  onChange: (block: TextBlock) => void
}

export default function TextPanel({ block, onChange }: TextPanelProps) {
  const updateStyle = (styles: Record<string, string | null>) => {
    const updatedHtml = updateHtmlStyle(block.html, styles)
    onChange({ ...block, html: updatedHtml })
  }

  return (
    <div className="p-4 space-y-4 text-sm">
      {/* ============ TEXT CONTENT ============ */}
      <div>
        <label className="block mb-1 font-medium">Text</label>
        <textarea
          className="w-full h-28 border rounded-md p-2"
          value={block.html.replace(/^<[^>]+>|<\/[^>]+>$/g, "")}
          onChange={(e) =>
            onChange({
              ...block,
              html: `<p>${e.target.value}</p>`,
            })
          }
        />
      </div>

      {/* ============ TEXT STYLE CONTROLS ============ */}
      <div>
        <label className="block mb-1 font-medium">Formatting</label>

        <div className="flex flex-wrap gap-2">

          {/* BOLD */}
          <Button
            size="sm"
            variant="outline"
            onClick={() => updateStyle({ "font-weight": "bold" })}
          >
            <Bold className="h-4 w-4" />
          </Button>

          {/* ITALIC */}
          <Button
            size="sm"
            variant="outline"
            onClick={() => updateStyle({ "font-style": "italic" })}
          >
            <Italic className="h-4 w-4" />
          </Button>

          {/* UNDERLINE */}
          <Button
            size="sm"
            variant="outline"
            onClick={() => updateStyle({ "text-decoration": "underline" })}
          >
            <Underline className="h-4 w-4" />
          </Button>

          {/* ALIGN LEFT */}
          <Button
            size="sm"
            variant="outline"
            onClick={() => updateStyle({ "text-align": "left" })}
          >
            <AlignLeft className="h-4 w-4" />
          </Button>

          {/* ALIGN CENTER */}
          <Button
            size="sm"
            variant="outline"
            onClick={() => updateStyle({ "text-align": "center" })}
          >
            <AlignCenter className="h-4 w-4" />
          </Button>

          {/* ALIGN RIGHT */}
          <Button
            size="sm"
            variant="outline"
            onClick={() => updateStyle({ "text-align": "right" })}
          >
            <AlignRight className="h-4 w-4" />
          </Button>

          {/* JUSTIFY */}
          <Button
            size="sm"
            variant="outline"
            onClick={() => updateStyle({ "text-align": "justify" })}
          >
            <AlignJustify className="h-4 w-4" />
          </Button>

        </div>
      </div>

      {/* ============ FONT SIZE ============ */}
      <div>
        <label className="block mb-1 font-medium">Font size</label>
        <Input
          type="number"
          defaultValue={16}
          onChange={(e) => updateStyle({ "font-size": `${e.target.value}px` })}
        />
      </div>

      {/* ============ TEXT COLOR ============ */}
      <div>
        <label className="block mb-1 font-medium">Text color</label>
        <input
          type="color"
          className="h-8 w-12 p-0 border rounded"
          onChange={(e) => updateStyle({ color: e.target.value })}
        />
      </div>

      {/* ============ BACKGROUND COLOR ============ */}
      <div>
        <label className="block mb-1 font-medium">Background color</label>
        <input
          type="color"
          className="h-8 w-12 p-0 border rounded"
          onChange={(e) => updateStyle({ "background-color": e.target.value })}
        />
      </div>

      {/* ============ LINE HEIGHT ============ */}
      <div>
        <label className="block mb-1 font-medium">Line height</label>
        <Input
          type="number"
          step="0.1"
          defaultValue={1.4}
          onChange={(e) => updateStyle({ "line-height": e.target.value })}
        />
      </div>

      {/* ============ LETTER SPACING ============ */}
      <div>
        <label className="block mb-1 font-medium">Letter spacing</label>
        <Input
          type="number"
          step="0.5"
          defaultValue={0}
          onChange={(e) => updateStyle({ "letter-spacing": `${e.target.value}px` })}
        />
      </div>
    </div>
  )
}
